.filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.filter__title {
  .indents-none();
  font-weight: @bold;
  font-size: inherit;
  text-transform: uppercase;
  color: @darkblue;
}

.filter__icon {
  margin-right: 8px;
  fill: @icon-darkblue;
}

.filter__title-colon {
  display: none;
}

.filter__toggle {
  .indents-none();
  position: relative;
  width: 17px;
  height: 6px;
  margin-right: 4px;
  font: inherit;
  text-align: left;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    background-color: @darkblue;
  }

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.filter__toggle-title {
  display: none;
}

.filter__regions-list {
  .indents-none();
  margin-top: 30px;
  margin-bottom: 20px;
  padding-left: 25px;
}

.filter__letter-selector {
  margin-left: 25px;
  margin-bottom: 32px;
}

.filter__collapse {
  position: relative;
  width: 270px;
  margin-bottom: 15px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 82px;
    width: 16px;
    height: 1.5px;
    border-radius: 3px;
    background-color: @darkblue;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

@media (min-width: @tablet-width) {
  .filter {
    align-items: flex-start;
  }

  .filter__toggle {
    order: 2;
    width: 176px;
    min-height: 38px;
    margin-right: 35px;
  }

  .filter__toggle-title {
    font-size: 20px;
    line-height: 24px;
    font-weight: @bold;
    text-transform: uppercase;
    color: @darkblue;
    cursor: pointer;

    &--collapse {
      padding-left: 2px;
    }
  }

  .filter__regions-list {
    order: 1;
    .indents-none();
    margin-left: 34px;
  }

  .filter__letter-selector {
    order: 3;
    .indents-none();
    margin-top: 35px;
    margin-left: -5px;
  }

  .filter__collapse {
    order: 4;
    width: 618px;
    height: 100px;
    margin: 0 auto;
    margin-top: 41px;
    margin-bottom: 28px;

    &::before,
    &::after {
      top: 48px;
      left: 239px;
      width: 25px;
      height: 2px;
      border-radius: 4px;
    }
  }
}

@media (min-width: @desktop-width) {
  .filter {
    justify-content: flex-start;
  }

  .filter__title {
    order: 1;
    align-self: center;
    margin-left: 20px;
    // padding-left: 43px;
    font-size: 20px;
    line-height: 24px;
  }

  .filter__icon {
    width: 23px;
    height: 15px;
    margin-right: 14px;
  }

  .filter__title-colon {
    display: inline;
  }

  .filter__regions-list {
    order: 2;
    margin-left: 34px;
    padding-top: 1px;
  }

  .filter__toggle {
    order: 3;
    margin-left: 371px;
  }

  .filter__letter-selector {
    order: 4;
    margin-top: 15px;
    margin-left: -25px;
  }

  .filter__collapse {
    order: 5;
    width: 100%;
    margin-top: 10px;

    &::before,
    &::after {
      left: 590px;
    }
  }
}
